import { Map, TileLayer, Popup, Marker } from "react-leaflet";
import { latLngBounds } from "leaflet";
import { Space } from "antd";
import { useGeolocated } from "react-geolocated";
import {
  CaretDownOutlined,
  CaretRightOutlined,
  SearchOutlined,
} from "@ant-design/icons";
import "./map.css";
import "leaflet/dist/leaflet.css";
import convert from "geo-coordinates-parser";
import L from "leaflet";
import { Divider } from "antd";
import { useState, useEffect } from "react";
import Select from "rc-select";
import { concat } from "geo-coordinates-parser/testformats";
var jsonKarabi = require("../data/kadastr_karabi.json");
var jsonBabugan = require("../data/kadastr_babugan.json");
var jsonBahchisaraj = require("../data/kadastr_bahchisaraj.json");
var jsonDemerdji = require("../data/kadastr_demerdji.json");
var jsonDolgorukovka = require("../data/kadastr_dolgorukovskaya.json");
var json = [
  ...jsonBabugan,
  ...jsonKarabi,
  ...jsonBahchisaraj,
  ...jsonDemerdji,
  ...jsonDolgorukovka,
];
const DevicesPage = () => {
  const [layer, setlayer] = useState(
    "https://cdn.lima-labs.com/{z}/{x}/{y}.png?api=demo"
  );

  const setLayerHandle = (layerCode) => {
    if (layerCode === "sattelite") {
      setlayer(
        "https://{s}.maptiles.arcgis.com/arcgis/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
      );
    }
    if (layerCode === "osm") {
      setlayer("https://cdn.lima-labs.com/{z}/{x}/{y}.png?api=demo");
    }
    return console.log("caveDistChange");
  };

  const { coords, isGeolocationAvailable, isGeolocationEnabled } =
    useGeolocated({
      positionOptions: {
        enableHighAccuracy: false,
      },
      userDecisionTimeout: 5000,
    });
  const [myGeo, setMyGeo] = useState([]);
  const position = [44.873, 34.575];
  // {lat: 45.20072775203464, lng: 34.092893600463874}
  const defaultBounds = [
    [44.745197, 33.367692],
    [33.862273, 45.209966],
  ];

  useEffect(() => {
    !isGeolocationAvailable
      ? console.log("Your browser does not support Geolocation")
      : !isGeolocationEnabled
      ? console.log("Geolocation is not enabled")
      : coords
      ? setMyGeo([coords.latitude, coords.longitude])
      : //console.log("latitude", coords.latitude, "longitude", coords.longitude)
        console.log("Getting the location data");
  }, [coords]);

  const EasyCaveIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
    iconSize: [24, 24],
    iconAnchor: [12, 12],
    popupAnchor: [0, 0],
  });

  const myGeoIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
    iconSize: [24, 24],
    iconAnchor: [12, 12],
    popupAnchor: [0, 0],
  });

  const SuperCaveIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
    iconSize: [24, 24],
    iconAnchor: [12, 12],
    popupAnchor: [0, 0],
  });

  const CustomCaveIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
    iconSize: [24, 24],
    iconAnchor: [12, 12],
    popupAnchor: [0, 0],
  });

  return (
    <div>
      <div className="alphaback" style={{ padding: "0 15px" }}>
        <span>
          <Space>
            Cлой карты:
            <select onChange={(el) => setLayerHandle(el.target.value)}>
              <option key={0} value="osm">
                OpenStreetMap
              </option>
              <option key={1} value="sattelite">
                Cпутник
              </option>
            </select>
          </Space>
        </span>
      </div>
      <Map
        className="simpleMap"
        center={position}
        zoom={9}
        key={0}
        onclick={(event) => console.log(event.latlng)}
      >
        {console.log()}
        <TileLayer
          attribution={"FollowMyTrack " + myGeo[0] + " " + myGeo[1]}
          className="basemap"
          maxNativeZoom={19}
          maxZoom={19}
          subdomains={["clarity"]}
          url={layer}
        />

        {myGeo.length > 0 && <Marker icon={myGeoIcon} position={myGeo} />}

        {json.map((el) => (
          <Marker
            icon={
              el.deep < 25
                ? EasyCaveIcon
                : el.deep > 90
                ? SuperCaveIcon
                : CustomCaveIcon
            }
            position={[
              convert(el.location).decimalLatitude,
              convert(el.location).decimalLongitude,
            ]}
          >
            <Popup>
              <b>Пещера {el.cave_name} </b>
              <br />
              Амплитуда: {el.deep}
              <br />
              Протяженность: {el.length}
              <br />
              Высота входа: {el.alt_entrance ? el.alt_entrance : "?"}
              <br />
              Координаты: {el.location}
            </Popup>
          </Marker>
        ))}
      </Map>
    </div>
  );
};

export default DevicesPage;
